.link{
    text-align: center;
}

.item{
    margin: 20px;
}
button{
    background-color: #93FFE8;
    padding: 10px;
    border: 1px solid #A4542C;
    width: 300px;
    color: #A4542C;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: bolder;
}

button.item{
    border-radius: 10px;
}

button:hover{
    background-color: #fad0b7 ;
}

a{
    text-decoration: none;
    color: #A4542C;
}
