


#habilap{
    flex-direction: row;
    display: flex;
    justify-content: space-between;
}
.aptitudes,
.habilidades{
   width: 45%;
   background-color: #FAD0B7;
   border: 1px solid #A4542C;
   text-align: center;
   border-radius: 20px;
}
.aptitudes li{
    border: none;
    padding: 5px;
    text-align: justify;
    color: #A4542C;
    list-style: none;

}

.aptitudes2 i{
    color: aquamarine;
    padding: 10px;
}
.aptitudes h3{
    color: #A4542C;
}

ul.aptitudes{
    padding: 10px;
}
.habilidades i{
    text-shadow: none;
    padding: 10px;
}

.icon{
    text-align: left;
    padding: 10px;
}

#habilap h3{
    text-decoration: underline;
    padding: 10px;
}

progress[value]{
    width: 50%;
    height: 10px;
    appearance: none;    
}

progress::-moz-progress-bar{
    background-color: blue;
}

progress::-webkit-progress-value{
    background: aquamarine;
}

span{
    color: #A4542C;
}

@media screen and(min-width:768px) {
    .habilidades{
        display: flex;
        flex-direction: column;
    }
    
}